import { Component, OnInit, Input } from '@angular/core';
import { TimelineItem } from './models/TimelineItem';
import { trigger, style, state, animate, transition, sequence, query } from '@angular/animations';

@Component({
  selector: 'app-timeline',
  templateUrl: './timeline.component.html',
  styleUrls: ['./timeline.component.scss'],
  animations: [
    trigger('readMoreAnimation', [
      state('*', style({ height: '*' })),
      transition('* => true', sequence([
        style({ height: 0 }),
        animate('250ms linear', style({ height: '*' }))
      ])),
      transition('true => false', sequence([
        style({ opacity: '*'}),
        animate('250ms linear', style({ opacity: 0 }))
      ]))
    ])
  ]
})
export class TimelineComponent implements OnInit {


  @Input('timeline') 
  public timeline: TimelineItem[] = [];

  constructor() { }

  ngOnInit() {

  }

  toggleReadMore(index: number) {
    this.timeline[index].readMore = !this.timeline[index].readMore;
  }
  
  toggleAnimating(index: number) {
    this.timeline[index].animating = !this.timeline[index].animating;
  }

}
