import { Component, OnInit } from '@angular/core';
import { trigger, transition, query, style, animate, group, sequence } from '@angular/animations';
import { RouterOutlet, Router } from '@angular/router';
import { ApiService } from './services/api.service';


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
  animations: [
    trigger('routeAnimations', [
      transition('* <=> *', [
        query(':enter, :leave', style({ position: 'absolute', top: 0, left: 0, right: 0, bottom: 0, boxShadow: '0 0 1.5rem 0 var(--black-50)' }), { optional: true }), /* both initial style */
        query(':leave', style({ zIndex: -1 }), { optional: true }),
        query('.page-component', style({ backgroundColor: 'white', }), { optional: true }), /* entering initial state */
        group([
          sequence([
            query(':enter', /* entering component new state animation */
            group([ 
              sequence([
                style({ transform: 'translate(-100%, 0) scale(0.95)' }),
                animate('500ms ease-out', style({ transform: 'translate(0) scale(1)' }))
              ]) 
            ]), { optional: true })
          ])
        ])
      ])
    ])
  ]
})
export class AppComponent implements OnInit {

  constructor(private apiService: ApiService, private router: Router) {
    if(window.location.hostname.includes('github')) this.githubHack();
  }

  githubHack() {
    /* github hack: serves as web.config */
    /* working with 404.html */
    let path = localStorage.getItem('path');
    if(path) {
      localStorage.removeItem('path');
      this.router.navigate([path]);
    }
  }

  ngOnInit() {
    this.apiService.showLoad();
  }
  
  animateRoute(outlet: RouterOutlet) {
    return outlet && outlet.activatedRouteData
  }

  get loading() {
    return this.apiService.loading.length
  }

}
