import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';


const routes: Routes = [
  { 
    path: '',
    pathMatch: 'full',
    redirectTo: 'home'
  }, 
  { 
    path: 'home', 
    loadChildren: () => import('./modules/home/home.module').then(m => m.HomeModule) 
  }, 
  { 
    path: 'about', 
    loadChildren: () => import('./modules/about/about.module').then(m => m.AboutModule)
  },
  { path: 'education', loadChildren: () => import('./modules/education/education.module').then(m => m.EducationModule) },
  { path: 'experience', loadChildren: () => import('./modules/experience/experience.module').then(m => m.ExperienceModule) },
  { path: 'work', loadChildren: () => import('./modules/work/work.module').then(m => m.WorkModule) },
  { path: 'contact', loadChildren: () => import('./modules/contact/contact.module').then(m => m.ContactModule) },
  { path: 'skill', loadChildren: () => import('./modules/skill/skill.module').then(m => m.SkillModule) },
/* 	{
		path: '**',
		redirectTo: '404-not-found'
	} */
];

@NgModule({
  imports: [
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
