import { Component, OnInit } from '@angular/core';
import { SidemenuControllerService } from '../sidemenu/services/sidemenu-controller.service';

@Component({
  selector: 'app-toggle-menu',
  templateUrl: './toggle-menu.component.html',
  styleUrls: ['./toggle-menu.component.scss']
})
export class ToggleMenuComponent implements OnInit {

  constructor(private sidemenuControllerService: SidemenuControllerService) { }

  ngOnInit() {
  }

  toggleSidemenu() {
    this.sidemenuControllerService.toggle();
  }

}
