import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse } from '@angular/common/http';
import { ContactMessage } from '../model/ContactMessage';
import { LoadingItem } from '../enum/loading-item.enum';

@Injectable({
  providedIn: 'root'
})
export class ApiService {
  public loading: LoadingItem[] = [];
  public host = 'https://api.iamrafaelperez.com';

  constructor(private http: HttpClient) { }

  showLoad() {
    this.addLoading(LoadingItem.LOADING_SHOW);
    setTimeout(() => {
      this.removeLoading(LoadingItem.LOADING_SHOW);
    }, 3000)
  }

  contactSend(contactMessage: ContactMessage): Promise<any> {
    
    this.addLoading(LoadingItem.CONTACT_SEND);
    return this.http.post<any>(this.host + '/contact/send', contactMessage).toPromise()
      .catch(e => console.log(e))
      .finally(() => this.removeLoading(LoadingItem.CONTACT_SEND));

  }
  
  addLoading(item: LoadingItem): void {
    this.loading.push(item);
  }

  removeLoading(item: LoadingItem): void {

    var index = this.loading.indexOf(item);
    this.loading.splice(index, 1);

  }

/*   handleHttpErrorResponse(error: HttpErrorResponse, loadingItem: LoadingItem): Promise<HttpErrorResponse> {

    this.removeLoading(loadingItem);
    return Promise.reject(error);

  } */
}
