import { Component, OnInit } from '@angular/core';
import { transition, trigger, state, animate, style, query } from '@angular/animations';
import { SidemenuControllerService } from './services/sidemenu-controller.service';
import { SidemenuItem } from './models/SidemenuItem';

@Component({
  selector: 'app-sidemenu',
  templateUrl: './sidemenu.component.html',
  styleUrls: ['./sidemenu.component.scss'],
  animations: [
    trigger('toggleSidemenuAnimation', [
      state('false', style({ transform: 'translateX(-100%)' })),
      state('true', style({ transform: 'translateX(0)' })),
      transition('* <=> false', animate('250ms ease')),
      transition('* <=> true', animate('250ms ease')),
    ])
  ]
})
export class SideMenuComponent implements OnInit {

  public sidemenus: SidemenuItem[] = [
    {
      id: 1,
      text: 'Home',
      link: '/home'
    },
    {
      id: 2,
      text: 'About',
      link: '/about'
    },
    {
      id: 3,
      text: 'Education',
      link: '/education'
    },
    {
      id: 4,
      text: 'Experience',
      link: '/experience'
    },
    {
      id: 5,
      text: 'Skill',
      link: '/skill'
    },
    {
      id: 6,
      text: 'Work',
      link: '/work'
    },
    {
      id: 7,
      text: 'Contact',
      link: '/contact'
    }
  ];

  constructor(public sidemenuController: SidemenuControllerService) { }

  ngOnInit() {
    (window as any).particlesJS.load('side-particles-js', '/assets/json/side.particlesjs-config.json');
  }

  closeSidemenu() {
    this.sidemenuController.close();
  }

}
