import { Component, OnInit, Input, Output, EventEmitter, AfterViewInit, AfterContentInit, HostListener } from '@angular/core';
import { style, animate, AnimationBuilder, AnimationFactory } from '@angular/animations';

@Component({
  selector: 'app-modal',
  templateUrl: './modal.component.html',
  styleUrls: ['./modal.component.scss']
})
export class ModalComponent implements OnInit {

  @Input()
  public header: string;

  @Input()
  public footer: string;

  @Input()
  public showHeader: boolean = true;

  @Output()
  public closeModal: EventEmitter<any> = new EventEmitter<any>();

  public modalAnimate: boolean = false;
  
  constructor(private animationBuilder: AnimationBuilder) { }

  ngOnInit() {
  }

  close() {
    this.closeModal.emit();
  }

  openAnimate(element) {

    return new Promise((resolve, reject) => {

      var openAnimation = this.animationBuilder.build([
        style({ transform: 'scale(0.5) translateY(100%)', opacity: 0 }),
        animate(150, style({ transform: 'scale(1) translateY(0)', opacity: 1 }))
      ]);
  
      var player = openAnimation.create(element);
      player.onDone(() => resolve())
  
      player.play();

    });

  }

  closeAnimate(element: HTMLElement) {

    return new Promise((resolve, reject) => {

      var closeAnimation = this.animationBuilder.build([
        style({ transform: 'scale(1) translateY(0)', opacity: 1 }),
        animate(150, style({ transform: 'scale(0.5) translateY(100%)', opacity: 0 }))
      ]);

      var player = closeAnimation.create(element);
      player.onDone(() => resolve())

      player.play();

    })
    
  }

}
