export default "/* Media Queries in Mobile First Approach */\n/* Media Queries in Web First Approach */\n.timeline {\n  --timeline-gap-to-middle: 0.5rem;\n}\n.timeline .timeline-item {\n  display: grid;\n  grid-template-columns: auto 1fr;\n}\n.timeline .timeline-item .date {\n  display: none;\n}\n.timeline .timeline-item:not(:last-child) > .detail {\n  padding-bottom: 6rem;\n  /* add timeline line for each item except last */\n  /* last item will get the excess line from second to the last because of `top: -0.175rem` */\n}\n.timeline .timeline-item:not(:last-child) > .detail::before {\n  position: absolute;\n  content: \" \";\n  border-left: 0.1rem solid var(--light-gray);\n  top: var(--timeline-gap-to-middle);\n  left: 0;\n  height: 100%;\n}\n.timeline .timeline-item .detail {\n  position: relative;\n  padding-left: 1.5rem;\n  /* timeline dot will rely on detail */\n}\n.timeline .timeline-item .detail > div {\n  padding-bottom: 0.3rem;\n}\n.timeline .timeline-item .detail::after {\n  position: absolute;\n  content: \" \";\n  top: var(--timeline-gap-to-middle);\n  left: -0.15rem;\n  height: 0.4rem;\n  width: 0.4rem;\n  border-radius: 50%;\n  background-color: var(--black);\n}\n.timeline .timeline-item .detail .title {\n  font-weight: bold;\n}\n.timeline .timeline-item .detail .location,\n.timeline .timeline-item .detail .date {\n  display: grid;\n  grid-template-columns: 1rem 1fr;\n  gap: 0.3rem;\n  align-items: center;\n}\n.timeline .timeline-item .detail .location svg,\n.timeline .timeline-item .detail .date svg {\n  height: 1rem;\n  width: auto;\n  justify-self: center;\n}\n.timeline .timeline-item .detail .description {\n  padding: 0.6rem 1rem 0.6rem;\n  text-indent: 3rem;\n  text-align: justify;\n}\n.timeline .timeline-item .detail .description .read-more,\n.timeline .timeline-item .detail .description .read-less {\n  text-indent: 0;\n}\n.timeline .timeline-item .detail .description .read-more a,\n.timeline .timeline-item .detail .description .read-less a {\n  color: var(--accent);\n}\n.timeline .timeline-item .detail .description ul {\n  overflow-y: hidden;\n  padding-top: 0.6rem;\n  font-size: inherit;\n  list-style-position: outside;\n  text-indent: 0rem;\n  padding-left: 1.5rem;\n}\n.timeline .timeline-item .detail .description ul.collapse {\n  display: none;\n}\n.timeline .timeline-item .detail .description ul li {\n  padding: 0.3rem;\n}\n.timeline .timeline-item .detail .keypoint {\n  max-width: 75%;\n}\n.timeline .timeline-item .detail .keypoint .label {\n  padding-top: 0.175rem;\n}\n.timeline .timeline-item .detail .keypoint .tag:not(:first-child) {\n  margin-top: 0.3rem;\n  margin-left: 0.3rem;\n}\n@media (min-width : 480px) {\n  .timeline .timeline-item .date {\n    display: block;\n    padding-right: 1rem;\n    padding-top: 0.175rem;\n    color: var(--gray);\n  }\n  .timeline .timeline-item .detail .date {\n    display: none;\n  }\n  .timeline .timeline-item .detail .description .read-more {\n    display: none;\n  }\n  .timeline .timeline-item .detail .description ul {\n    display: block !important;\n    padding-left: 4.5rem;\n  }\n}\n@media (min-width : 992px) {\n  .description {\n    padding: 0.6rem 3rem 0.6rem 4.5rem;\n  }\n}\n/*# sourceMappingURL=data:application/json;base64, */"