import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class SidemenuControllerService {

  public show: boolean = false;

  constructor() { }

  toggle(): void {
    this.show = !this.show;
  }

  open(): void {
    this.show = true;
  }

  close(): void {
    this.show = false;
  }
}
